
.error-container {
    background: #F8F4F4;
    border:  1px solid #ECECEC;
    /*width: 90%;*/
    text-align: center;
    color: #9B9B9B;
    vertical-align: center;
    margin: 0 auto;
    line-height: 24px;
    padding: 9% 5%;
}

.error-container p{
    line-height: 20px;
    font-size: 18px;
}

.btn-error {
    font-size: 11px;
    padding: 15px 65px;
    border: 1px solid #979797;
    border-radius: 6px 6px;;
    background: transparent;
    box-shadow: 0 0;
    color: #9B9B9B;
    cursor: pointer;
}

.btn-error:hover {
    color: #fff;
    background: #ef6a69;
}
