a{
  text-decoration: none !important;
}
a a:hover{
  background-color: red !important;
}

.btn.focus, .btn:focus, .btn:hover,.btn:active{
  color: white;
  outline:0;
  background: linear-gradient(180deg, #F49646 0%, #EE656D 100%);
  /* border:0px solid */
}
button:focus {outline:0;}

.loading{
  position: absolute;
  left: 13%;
  top: 50%;
}

.x.axis path {
  display: none;
}

.axis text {
  font-family: Roboto;
  font-size: 12px;
}
.x.axis path {
  display: none;
}

line {
  fill: #fff;
  stroke: #F49646;
  stroke-width: 1px;
  shape-rendering: crispEdges;
}
circle {
  opacity: 0.65;
  stroke: none;
  /* shape-rendering: crispEdges; */
}

.selected {
  /* stroke: red; */
  fill: #D6D6D6;
  opacity: 1;
  stroke-width: 2px;
}

.axis path {
  display: none;
  fill: none;
  stroke: black;
  stroke-width: 1px;
  shape-rendering: crispEdges;
}

.axis line {
  fill: none;
  display: none;
  stroke: #F49646;
  stroke-width: 1px;
  shape-rendering: crispEdges;
}

.axis text {
  font-family: 'Roboto', sans-serif !important;
  font-size: 14px;
  -moz-user-select: -moz-none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: default;
}

.yaxis{
  stroke: #737373;
  font-family: Roboto;
  font-size: 10px;
  font-weight: 300;
}

.page{
  padding-top: 5px;
  position:absolute;
  width: 33%;
  -webkit-animation-name: run;
  -webkit-animation-duration: 0.3s;
}
.animated {
  -webkit-animation-duration: 0.5s;
  animation-duration: 0.5s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.animated1 {
  background-position: left top;
  -webkit-animation-duration: 0.5s;
  animation-duration: 0.5s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.model-result-table {
  width: 550px;
  margin-top: 10px;
  border-collapse: collapse;
}

.model-result-table td {
  padding-left : 50px;
  font-size: 14px;
}

.model-result-table tr {
  line-height: 100px;
  border-bottom: 1px solid #F28355;
}

.model-result-table tr:last-child {
  border-bottom: none;
}

.model-result-table tr td:first-child {
  color: #666666;
}

.back-btn-stepper {
  background: transparent;
  border: none;
  color: lightgray;
  cursor: pointer;
  position: absolute;
  left: 15%;
  bottom: 22px;
  font-size: 24px;
}

.back-btn-stepper::before {
  content: '\f053';
  font-family: 'FontAwesome';
  font-weight: 100;
}
