.progress-upload-container {
    margin: 4px;
    border: 1px solid lightgray;
    border-radius: 3px;
    background: #f2f2f2;
    display: flex;
    height: 50px !important;
}

.progress-upload-container .progress-label{
    width: 100px;
    margin: 18px 0px;
    font-size: 11px;
    color: #757575;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.progress-upload-container .uploaded-progress{
    width: 100%;
    height: 10px;
    margin: 20px;
    display: flex;
    overflow: hidden;
    font-size: 0.75rem;
    background-color: #BDBDBD;
    border-radius: 5px;
}

.progress-upload-container .uploaded-progress .progress-bar{
    background: linear-gradient(90deg, #F49646 0%, #EE656D 100%);
}

.progress-upload-container .cancel-icon {
    width: 20px;
    cursor: pointer;
    margin: 15px 6px 0px -5px;
    height: 20px;
}
