.col-1, .col-2, .col-3 {
    float: left;
    width: 33.05%;
    position: relative;
}

.col-header {
    box-sizing: border-box;
    width: 100%;
    float: left;
    text-align: left;
    border-top:1px solid #ababab;
    border-bottom:1px solid #ababab;
    padding: 10px 15px;
    font-size: 14px;
}

.col-body {
    float: left;
    padding: 15px 18px;
}

.col-grid-left {
    float: left;
    width: 25%;
    margin-left: 10%;
}

.col-grid-left .col-grid {
    width: 25%;
    float: left;
    padding: 6px 10px;
}

.col-grid-right {
    float: left;
    width: 60%;
    margin-left: 3%;
    margin-top: 10%;
}

.col-grid {
    width: 22%;
    float: left;
    padding: 10px 10px;
}
.light-grey {
    background: #F1F1F1;
}

.dark-grey {
    background: #A5A5A5;
}
.light-orange {
    background: #FFD1B4;
}

.dark-orange {
    background: #F3904A;
}

.head_name {
    width: 100%;
    float: left;
}

span.act-pred {
    position: absolute;
    left: 41%;
    bottom: 10%;
}

label.label-left, label.label-right{
    text-align: left;
    width: 45%;
    float: left;
    padding: 5px;
    font-size: 15px;
    font-weight: 500;
}

.table-wrap-first-right {
    float: right;
    margin-top: -8px;
    margin-right: 30px;
}

label.table-wrap-label {
    padding-right: 20px;
    border-right: 1px solid rgb(189, 189, 189);
    display: inline-block;
    line-height: 1.8;
    font-family: Roboto;
    position: relative;
    margin: 10px 20px 10px 0;
}

span {
    display: inline-block;
    line-height: 1.8;
    font-family: Roboto;
    font-size: 14px;
}

span.ReactVirtualized__Table__headerTruncatedText {
    display: inline-block;
    line-height: 1.8;
    font-family: Roboto;
    font-size: 14px;
}

.col-grid-left-col {
    width: 100%;
    text-align: center;
    margin: 0 auto;
}

.label-left-false {
    text-align: left;
    width: 40%;
    float: left;
    padding: 5px;
    font-size: 10px;
    font-weight: 500;
}

.label-right-false {
    text-align: right;
    width: 47%;
    float: left;
    padding: 5px;
    font-size: 10px;
    font-weight: 500;
}

.border-left {
    border-left: 1px solid #ababab;
}

.text-light-grey {
    color: #C2C2C2;
}

.text-grey {
    color: #9D9D9D;
}

.text-dark-grey {
    color: #7F7F7F;
}

.m-l-15 {
    margin-left: 15%;
}

.text-acc {
    font-size: 11px;
    float: left;
    width: 100%;
}

.acc {
    float: left;
    width: 45%;
    text-align: left;
}

.num {
    float: left;
    width: 45%;
    text-align: right;
}

.m-b-15{
    margin-bottom: 15%;
}

.m-t-5 {
    margin-top: 5%;
}

.m-t-10 {
    margin-top: 10%;
}

.m-t-20 {
    margin-top: 20%;
}

.border-bottom {
    border-bottom: 1px solid #ababab;
}

.toolTip {
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    position: absolute;
    display: none;
    width: auto;
    height: auto;
    background: none repeat scroll 0 0 #f3f3f3;
    border: 0 none;
    box-shadow: -3px 3px 15px #888888;
    color: #ff8000;
    padding: 5px;
    text-align: center;
}

.txt-lime-c {
    font-size: 12px;
    line-height: 100px;
    vertical-align: middle;
    margin-left: 1px;
    box-sizing: border-box;
    padding-left: 8px;
    text-align: start;
}

.headerIcon-span-left {
    color: #BDBDBD;
    position: absolute;
    font-size: 22px;
    font-weight: 500;
    left: 1px;
    cursor: pointer;
    margin-top: 40px;
}

.headerIcon-span-right {
    color: #BDBDBD;
    position: absolute;
    font-size: 22px;
    font-weight: 500;
    right:1px;
    cursor: pointer;
    margin-top: 40px;
}

.undo-icon{
    color: #757575;
    left: 50px;
    position: absolute;
    font-size: 22px;
    font-weight: 500;
    cursor: pointer;
}

.ReactVirtualized__Grid.ReactVirtualized__Table__Grid.virtulized-scroll{
    overflow-y: hidden !important;
    outline: none;
}

.ReactVirtualized__Grid__innerScrollContainer .ReactVirtualized__Table__row:nth-child(even) {
    background-color: #f3f3f3;
}

.ReactVirtualized__Table__headerColumn:first-of-type,
.ReactVirtualized__Table__rowColumn:first-of-type {
    margin-left: 0px !important;
}

.ReactVirtualized__Table__headerRow {
    background: #282828;
    text-transform : none !important;
    padding-right: 0px !important;
}

.ReactVirtualized__Table__headerColumn{
    color: #fff;
    font-size: 12px;
    font-weight: 300;
    height: inherit;
    box-sizing: border-box !important;
    border-left: 1px solid #757575 !important;
    margin-right: 0 !important;
    cursor: pointer;
    position: relative;
}

.ReactVirtualized__Table__headerColumn-before {
    content: "v";
    display: inline-block;
    padding-right: 3px;
    vertical-align: middle;
    font-weight: 900;
    position: absolute;
    right: 5px;
    top: 39%;
    color: rgb(189, 189, 189);
}

.ReactVirtualized__Table__headerColumn-after {
    content: ">";
    display: inline-block;
    padding-right: 3px;
    vertical-align: middle;
    font-weight: 900;
    position: absolute;
    right: 5px;
    top: 39%;
    color: rgb(189, 189, 189);
}

.ReactVirtualized__Table__headerTruncatedText{
    height: inherit;
    display: inline-flex !important;
    align-items: center;
}

.ReactVirtualized__Table__row {
    position: relative!important;
    top:0px!important;
}

.ReactVirtualized__Table__row-border-left{
    border-left: 1px solid rgba(117, 117, 117, 0.18)  !important;
}

.ReactVirtualized__Table__rowColumn{
    margin-right: 0px !important;
    font-size: 12px !important;
    color: #757575;
    box-sizing: border-box !important;
}

.header-active{
    border-bottom: 5px solid orangered;
}

.pagination{
    cursor: pointer;
    margin: 20px 0;
}

ul.pagination {
    position: relative;
    list-style: none;
    width: auto;
    float: right;
}

ul.pagination li {
    float: left;
}

ul.pagination li a{
    padding: 6px 20px;
}

ul.pagination li a:hover{
    background-color: #EE656D;
    border-color: #EE656D;
    color: #ffffff;
}

.pagination>.active>a, .pagination>.active>a:focus, .pagination>.active>span, .pagination>.active>span:focus, .pagination>.active>span:hover{
    background-color: #EE656D;
    border-color: #EE656D;
    color: #ffffff;
}

.pagination>li>a, .pagination>li>span{
    color: black;
}

.table-toolTip {
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    position: absolute;
    display: none;
    width: auto;
    height: auto;
    background: none repeat scroll 0 0 #EEEEEE;
    border: 0 none;
    box-shadow: -3px 3px 15px #f2f2f2;
    color: #757575;
    font-size: 10px;
    padding: 5px;
    text-align: center;
}

.table-header-toolTip {
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    position: absolute;
    display: none;
    width: auto;
    height: auto;
    background: none repeat scroll 0 0 transparent;
    border: 0 none;
    color: #757575;
    font-size: 10px;
    padding: 5px;
    text-align: center;
}

.table-toolTip table {
    border-color: darkgray !important;
}

.backToTable-chart {
    position: absolute;
    right: 20px;
    cursor: pointer;
    background: #BDBDBD;
    border-radius: 50%;
    width: 20px;
    top:55px;
}

.table-chart-bookmark {
    position: absolute;
    right: 50px;
    cursor: pointer;
    border-radius: 50%;
    width: 30px;
}
