
.react-bs-table-container tr th {
    border :1px solid #757575 ;
    background:#282828;
    color:white;
    width:100px;
    height:50px;
    text-align: center !important;
    line-height: 1.8;
    font-family: Roboto;
    font-size: 14px;
    font-weight: 300;

}
.react-bs-table-container tr td:first-child {
    text-align: center !important;
    margin-right: 0px !important;
    font-size: 14px !important;
    color: #757575 !important;
    box-sizing: border-box !important;
    width: 100px;
    min-width: 600px;
    height:50px;
    cursor: pointer;

}
.react-bs-table-container tr td:last-child {
    width:100px;
    text-align: center !important;
    margin-right: 0px !important;
    font-size: 14px !important;
    color: #757575 !important;
    box-sizing: border-box !important;
    min-width: 600px;
    cursor: pointer;


}
.react-bs-table-container .table{
    border-collapse:collapse !important;
    width:100%;
    font-family: Roboto;
    font-weight: 300;
}

.selectedTable .tr {
    height:20px;
}

.react-bs-table-container tr:nth-child(even) {
    background-color: #f2f2f2;
}
.react-bs-table-pagination .dropdown{
    display:none!important;
}
 .react-bs-table-container input{
    width: 20px !important;
    height: 40px !important;
}