.stepper-main {

}
.md-stepper-horizontal {
    display:table;
    width:100%;
    margin:0 auto;
    background-color:#FFFFFF;
    box-shadow: 0 3px 8px -6px rgba(0,0,0,.50);
}
.md-stepper-horizontal .md-step {
    display:table-cell;
    position:relative;
    padding:24px;
    font-family: Roboto, sans-serif;
}
.md-stepper-horizontal .md-step:active {
    border-radius: 15% / 75%;
}
.md-stepper-horizontal .md-step:first-child:active {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}
.md-stepper-horizontal .md-step:last-child:active {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}
.md-stepper-horizontal .md-step:hover .md-step-circle {
    background-color:#757575;
}
.md-stepper-horizontal .md-step:first-child .md-step-bar-left,
.md-stepper-horizontal .md-step:last-child .md-step-bar-right {
    display:none;
}
.md-stepper-horizontal .md-step .md-step-circle {
    width:30px;
    height:30px;
    margin:0 auto;
    background-color:#999999;
    border-radius: 50%;
    text-align: center;
    line-height:30px;
    font-size: 16px;
    font-weight: 600;
    color:#FFFFFF;
}
.md-stepper-horizontal.green .md-step.active .md-step-circle {
    background-color:#00AE4D;
}
.md-stepper-horizontal.orange .md-step.active .md-step-circle {
    background-color:#F96302;
}
.md-stepper-horizontal .md-step.active .md-step-circle {
    background-color: rgb(33,150,243);
}
.md-stepper-horizontal.orange .md-step.active .btn{
    background-image: linear-gradient(#f08355 25%, #ef6a69);
    border-radius: 3px;
    box-shadow: 0 0;
    border: 0 none;
    padding: 8px 35px;
    color: #fff;
    font-size: 12px;
    outline: none;
    cursor: pointer;
}
.md-stepper-horizontal.orange .md-step.visited .btn{
    background-image: linear-gradient(#acacac 25%, #666);
    border-radius: 3px;
    box-shadow: 0 0;
    border: 0 none;
    padding: 8px 35px;
    color: #fff;
    font-size: 12px;
    outline: none;
}
.md-stepper-horizontal .md-step.visited .btn{
    cursor: pointer;
}
/*.md-stepper-horizontal .md-step.done .md-step-circle:before {
    font-family:'FontAwesome';
    font-weight:100;
    content: "\f00c";
}*/
.md-stepper-horizontal .md-step.done .md-step-circle *,
.md-stepper-horizontal .md-step.editable .md-step-circle * {
    display:none;
}
.md-stepper-horizontal .md-step.editable .md-step-circle {
    -moz-transform: scaleX(-1);
    -o-transform: scaleX(-1);
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
}
.md-stepper-horizontal .md-step.editable .md-step-circle:before {
    font-family:'FontAwesome';
    font-weight:100;
    content: "\f040";
}
.md-stepper-horizontal .md-step .md-step-title {
    margin-top:16px;
    font-size:12px;
    font-weight:400;
    font-family: Roboto, sans-serif;
}
.md-stepper-horizontal .md-step .md-step-title,
.md-stepper-horizontal .md-step .md-step-optional {
    text-align: center;
    color: #edeceb;
}
.md-stepper-horizontal .md-step .md-step-title .btn{
    background: #edeceb;
    border: 0 none;
    border-radius: 3px;
    box-shadow: 0 0;
    padding: 8px 35px;
    color: #fff;
    font-size: 12px;
    outline: none;
}
.md-stepper-horizontal .md-step.active .md-step-title {
    font-weight: 400;
    color:rgba(0,0,0,.87);
    font-size: 12px;
    font-family: Roboto, sans-serif;
}
.md-stepper-horizontal .md-step.visited .md-step-title {
    font-family: Roboto, sans-serif;
    font-weight: 400;
    color:rgba(0,0,0,.57);
    font-size: 12px;
}
.md-stepper-horizontal .md-step.active.done .md-step-title,
.md-stepper-horizontal .md-step.active.editable .md-step-title {
    font-weight: 400;
    font-size: 12px;
    font-family: Roboto, sans-serif;
}
.md-stepper-horizontal .md-step .md-step-optional {
    font-size:12px;
}
.md-stepper-horizontal .md-step.active .md-step-optional {
    color:rgba(0,0,0,.54);
}
.md-stepper-horizontal .md-step.visited .md-step-optional {
    color:rgba(0,0,0,.54);
}
.md-stepper-horizontal .md-step .md-step-bar-left,
.md-stepper-horizontal .md-step .md-step-bar-right {
    position:absolute;
    top:36px;
    height:1px;
    border-top:1px solid #edecea;
}
.md-stepper-horizontal .md-step.active .md-step-bar-left,
.md-stepper-horizontal .md-step.visited .md-step-bar-left,
.md-stepper-horizontal .md-step.visited .md-step-bar-right {
    position:absolute;
    /*top:36px;*/
    height:1px;
    border-top:1px solid #b2b2b2;
}
.md-stepper-horizontal .md-step .md-step-bar-right {
    top:30%;
    right:0;
    left:60%;
    margin-left:20px;
}
.md-stepper-horizontal .md-step .md-step-bar-left {
    top:30%;
    left:0;
    right:60%;
    margin-right:20px;
}

.content-stepper-horizontal {
    display:table;
    width:100%;
    margin:0 auto;
    background-color:#FFFFFF;
    box-shadow: 0 3px 8px -6px rgba(0,0,0,.50);
    border-top: 1px solid rgba(224, 224, 224, 0.59);
}

/* Content Stepper */
.content-stepper-horizontal .content-step {
    display:table-cell;
    position:relative;
    padding:24px 20px;
}
.content-stepper-horizontal .content-step:active {
    border-radius: 15% / 75%;
}
.content-stepper-horizontal .content-step:first-child:active {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}
.content-stepper-horizontal .content-step:last-child:active {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}
.content-stepper-horizontal .content-step:first-child .content-step-bar-left,
.content-stepper-horizontal .content-step:last-child .content-step-bar-right {
    display:none;
}
.content-stepper-horizontal .content-step:last-child .content-step-bar-left {
    right: 60%;
}
.content-stepper-horizontal .content-step.done .content-step-circle:before {
    font-weight:100;
    font-size: 14px;
}
.content-stepper-horizontal .content-step.done .content-step-circle * {
    display:none;
}
.content-stepper-horizontal .content-step .content-step-title {
    margin-top: 0;
    font-size:12px;
    font-weight:400;
    color: #d6d6d6;
}
.content-stepper-horizontal .content-step .content-step-title,
.content-stepper-horizontal .content-step .content-step-optional {
    text-align: center;
    color:#d6d6d6;
}
.content-stepper-horizontal .content-step.active .content-step-title {
    font-weight: 400;
    color:#999999;
    font-family: Roboto, sans-serif;
}
.content-stepper-horizontal .content-step.done .content-step-title{
    font-weight:400;
    color:#F96302;
    font-family: Roboto, sans-serif;
}
.content-stepper-horizontal .content-step .content-step-optional {
    font-size:12px;
}
.content-stepper-horizontal .content-step .content-step-bar-left,
.content-stepper-horizontal .content-step .content-step-bar-right {
    position:absolute;
    top:30px;
    height:1px;
    border-top:1px solid #DDDDDD;
}
.content-stepper-horizontal .content-step .content-step-bar-right {
    right:0;
    left:50%;
    margin-left:50px;
}
.content-stepper-horizontal .content-step .content-step-bar-left {
    left:0;
    right:50%;
    margin-right:50px;
}
.content-stepper-horizontal .content-step.done .content-step-bar-right,
.content-stepper-horizontal .content-step.active .content-step-bar-left,
.content-stepper-horizontal .content-step.done .content-step-bar-left {
    border-top:1px solid #F96302;
}

.stepper-horizontal-container {
    background: #F8F4F4;
    border:  1px solid #ECECEC;
    text-align: center;
    vertical-align: center;
    margin: 0 auto;
    line-height: 0px;
    padding: 6% 5%;
    font-family: Roboto, sans-serif;
}

.btn-cancel {
    font-size: 12px;
    padding: 10px 50px;
    border: 1px solid #979797;
    border-radius: 6px 6px;;
    background: transparent;
    box-shadow: 0 0;
    color: #9B9B9B;
    cursor: pointer;
    font-family: Roboto, sans-serif;
}

.btn-cancel:hover {
    color: #fff;
    background: #ef6a69;
    font-family: Roboto, sans-serif;
}
.back-btn-Prediction{
    background: transparent;
    border: none;
    color: lightgray;
    cursor: pointer;
    position: absolute;
    bottom: 23px;
    font-size: 24px;
    right: 15%;
}

.back-btn-Prediction::before {
    content: '\f054';
    font-family: 'FontAwesome';
    font-weight: 100;
}
